
import {
    ADD_CUSTOMER,
    ADD_CUSTOMERS,
    REMOVE_CUSTOMER,
    REMOVE_CUSTOMERS
} from '../actions/action_variables';

const filterListCustomers = ( currentList, arrCustomer ) => {
    
    for ( let j = 0; j < arrCustomer.length; j++ ) {
        if ( !checkExistsCustomer( currentList, arrCustomer[j] ) ) {
            currentList.push( arrCustomer[j] )
        }
    }

    return currentList;
}

const checkExistsCustomer = ( arrCustomer, customer ) => {
    for ( let i = 0; i < arrCustomer.length; i++ ) {
        if ( arrCustomer[i].email == customer.email ) {
            return true;
        }
    }

    return false;
}

const removeCustomer = ( arrCustomer, customer ) => {

    for ( let i = 0; i < arrCustomer.length; i++ ) {
        if ( arrCustomer[i].email == customer.email ) {
            arrCustomer.splice( i, 1 );
            return arrCustomer;
        }
    }

    return arrCustomer;
}

export const CustomerSelectedReducer = (customers = [], action) => {
    var result = [...customers];
    
    switch (action.type) {
        case ADD_CUSTOMER :
            
            if ( checkExistsCustomer( result, action.data ) )
                return result;
            else {
                result.push( action.data )
                return result;
            }
        case ADD_CUSTOMERS :
            return filterListCustomers( result, action.data )
        case REMOVE_CUSTOMER: 
            return removeCustomer( result, action.data )

        case REMOVE_CUSTOMERS: 
            return []
        default :
            return result;
    }
}